.cpsearch-filter-select--heading-group {
    display: inline-block;
}

.cpsearch-filter-select--clear-button {
    font-weight: 700 !important;
    font-size: 18px !important;
    padding: 0 !important;
    margin-left: 0.5rem !important;
}

.cpsearch-filter-select--heading {
    font-size: 20px !important;
    display: inline-block;
    margin-right: 0.5rem;
    margin-top: 20px;
    color: #151515;
    font-weight: 500 !important;
}

.cpsearch-filter-select--count {
    display: inline-block;
    position: relative;
    bottom: 3px;
}

.filter-select-ul {
    list-style-type: none;
    padding: 0;
    list-style: none;
    overflow-y: auto;
    overflow-x: hidden;
    position: relative;
    max-height: 355px;
}

/* input[type="checkbox"] {
    margin: 0;
} */

.cpsearch-searchbox{
    position: relative;
    margin: 0 12px 6px;
}

.search-icon, .clear-icon{
    --pfe-icon--Size: 1rem;
    position: absolute;
    right: -100px;
    top: 10px;

}

.pf-c-accordion{
  /* border-top: 1px solid #D2D2D2; */
    --pf-c-accordion__toggle--m-expanded--before--BackgroundColor:#EE0000 !important;
    --pf-c-accordion__expanded-content--m-expanded__expanded-content-body--before--BackgroundColor: #EE0000 !important;
    --pf-c-accordion__toggle--focus__toggle-text--Color: #151515;
    --pf-c-accordion__toggle--active__toggle-text--Color: #151515;
    --pf-c-accordion__toggle--hover__toggle-text--Color: #151515;
    --pf-c-accordion__expanded-content-body--PaddingLeft: 0;
    --pf-c-accordion__expanded-content-body--PaddingRight: 0;
    --pf-c-accordion__expanded-content-body--PaddingTop: 0;
    --pf-c-accordion__expanded-content-body--PaddingBottom: 24px;
    --pf-c-accordion__toggle--PaddingTop: 16px;
    --pf-c-accordion__toggle--PaddingRight: 16px;
    --pf-c-accordion__toggle--PaddingBottom: 16px;
    --pf-c-accordion__toggle--PaddingLeft: 24px;


    /* border-bottom: 1px solid #D2D2D2; */

    
}

dt{
    margin-bottom: 0 !important;
}
.pf-m-expanded{
    border-right: 1px solid #D2D2D2;
}
/* .pf-m-expanded{
    border-right: 1px solid #D2D2D2;
    
} */

.pf-c-accordion__toggle.pf-m-expanded .pf-c-accordion__toggle-text{
    font-weight: 500;
    color: #151515;
}
.pf-c-accordion__toggle:focus .pf-c-accordion__toggle-text {
    font-weight: 500;
}

.pf-c-accordion__toggle-text{
    font-family: 'Red Hat Display';
    font-style: normal;
    font-size: 20px;
    line-height: 30px;
    color: #151515;
    font-weight: 500;
}



/* .pf-c-accordion__toggle.pf-m-expanded{
    --pf-c-accordion__toggle--before--BackgroundColor: #EE0000 !important;
} */
/* TABLET ACCORDION STYLES*/
@media (max-width: 766px) {
    .pf-c-accordion{
        /* border-top: 1px solid #D2D2D2; */
          --pf-c-accordion__toggle--m-expanded--before--BackgroundColor:none !important;
          --pf-c-accordion__expanded-content--m-expanded__expanded-content-body--before--BackgroundColor: none !important;    
      }


      .search-icon, .clear-icon{
        right: -220px;
      }

}

@media(max-width: 480px) {
    .search-icon, .clear-icon{
        right: -150px;
      }

}