body{
  font-family: RedHatText;
}

.num-results-container{
  display: flex;
  align-items: baseline;
}

.total-results-heading-text{
  font-family: 'Red Hat Display';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 32px;
  color: #000000;
  margin-right: 8px;
}

.num-results-text{
  font-family: 'Red Hat Text';
  font-size: 16px;
  font-weight: 400;
}

.no-results-with-filter-text{
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #000000;
}

.no-results-with-filter-text-query{
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  color: #151515;
}

.dropdown-sort-container{
  display: flex;
  align-items: center;
}

.dropdown-sort-text{
  font-family: 'Red Hat Text';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #000000;
  margin-right: 16px;
}


.app-wrapper {
  display: grid;
  grid-template-rows: auto;
    grid-template-areas:
        ". input-wrapper input-wrapper input-wrapper"
        "sidebar results-wrapper results-wrapper results-wrapper";

  grid-template-columns: 1fr 2.5fr;
  padding: 40px 0;
}

.input-wrapper{
  grid-area: input-wrapper;
  max-width: 55%;
}

.input-bar-empty-state{
  max-width: 100%;
}

.sidebar{
  padding-left: 32px;
  padding-right: 28px;
  margin-right: 12px;
  margin-top: 56px;
  grid-area: sidebar;
}

.sidebar-hidden{
  grid-template-areas:
    ". input-wrapper input-wrapper ."
    "sidebar results-wrapper results-wrapper results-wrapper";
  grid-template-columns: auto;
  width: 100%;

}

.app-wrapper-loading{
  grid-template-areas:
  ". input-wrapper input-wrapper input-wrapper"
  "sidebar results-wrapper results-wrapper results-wrapper"; 
}

.sidebar-no-results{
  /* display: none;
  grid-template-columns: none; */
}

.filters-container-mobile{
  display: none;
}

.filter-text-container{
  background: #151515;
}

.filters-container{
  border-left: 1px solid #D2D2D2;
  border-right: 1px solid #D2D2D2;
  border-bottom: 1px solid #D2D2D2;
  border-radius: 5px 5px 3px 3px;
}
.clear-filter-container{
  padding: 16px;
}

.filter-text{
  font-family: 'Red Hat Text';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: #FFFFFF !important;
  padding: 16px;
}

.element-invisible, .sr-only, .visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.clear-all-buttom{
  margin: 0 0 16px 0 !important;
}

/* total results and dropdown styles */
.total-results-container{
  display: flex;
  margin: 32px 0 24px;
  flex-direction: column;
}

.total-results-sort-by-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}


/* result styles */
.total-results{
  margin: 32px 0;
  color: #151515;
  font-family: RedHatText;
  font-size: 18px;
  line-height: 27px;
}

.few-results-wrapper-desktop{
  border-top: 1px solid #D2D2D2;
  border-bottom: 1px solid #D2D2D2;
  padding: 16px 0;
  margin: 32px 16px 8px;
}

.few-results-wrapper-mobile{
  display: none;
}

.few-results-text{
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 0px;
  display: inline;
  margin-right: 8px;
}

.few-results-text-link{
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  text-decoration-line: underline;
  color: #0066CC;
}

.suggestion-state-wrapper{
  margin: auto;
  width: 50%;
}

.double-card-bordered-top-container{
  margin-top: 32px;
}


.total-results__bottom{
  margin: 48px 0;
}

#result-input-value{
  color:#0066CC;
}

.loading{
  width:100%;
  margin-right: 0;
}

.loading-container {
  margin-top:32px;
}

.pf-c-skeleton{
  --pf-c-skeleton--BackgroundColor: #949494 !important;
  --pf-c-skeleton--after--LinearGradientColorStop1: #949494 !important;
  --pf-c-skeleton--after--LinearGradientColorStop2: #828282 !important;
  --pf-c-skeleton--after--LinearGradientColorStop3: #949494 !important;
}

.single-skeleton-container{
  margin-bottom: 64px;
  margin-left: 16px;
}

.skeleton-item{
  margin-bottom: 12px !important;
}
.skeleton-description-item{
  margin-bottom: 8px !important;
}

.error-state__wrapper{
  margin-top: 48px;
}

.results-wrapper{
  margin-right: 32px;
  margin-top: 24px;
  grid-area: results-wrapper;
  max-width: 55%;
}

.results-wrapper-hidden{
  max-width: 70%;
}

.card-container{
  width: 45%;
  margin: auto;
}

.empty-state-container{
  width: 90%;
  margin: auto;
}

.troubleshoot-text{
  text-align: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  margin: 64px 0;
}
.troubleshoot-text a{
  text-decoration: underline;
}

.search__input-form-wrapper{
  width: 60%;
  margin: auto;
}

.search__input-form-wrapper-loading{
  width: 70%;
  margin:0;
}

.search__input-form-wrapper-with-result{
  width: 100%;
}

.pf-c-pagination{
  --pf-c-pagination--c-options-menu--Display: none !important;
  --pf-c-pagination--m-bottom--md--PaddingTop: 0 !important;
  --pf-c-pagination--m-bottom--md--PaddingBottom: 64px !important;
  align-items: baseline !important;
}


.pf-c-pagination__nav{
  align-items: baseline; 
}

.pf-c-divider{
  position: relative;
  z-index: 1;
}

/* TABLET STYLES */
@media (max-width: 766px) {
  .total-results{
    margin: 16px 0;
  }
  .card-container{
    width: 90%;
  }
.empty-state-container{
  width: 90%;
}

  .search__input-form-wrapper{
    width: 70%;
  } 
  .search__input-form-wrapper-with-result{
    width: 90%;
  }

  .search__input-pagination-wrapper{
    width: 100%;
  }
  

  .header{
    font-size: 18px;
  }

  .loading{
    width:95%;
    margin-right:16px;
  }

  .troubleshoot-text{
    font-size: 16px;
    margin: 48px 0;
  }
  
  .app-wrapper{
    display: grid;
    grid-column-start: 12;
    grid-template-columns: auto;
  }  

  .sidebar{
    display: none;
  }

  .filters-container-mobile{
    display: block;
    margin: 16px 0 24px;
  }

  .filter-heading-wrapper{
    padding: 16px;
  }

  .clear-filter-container{
    padding: 0;
  }

  .pf-c-expandable-section__toggle{
    background-color: #151515 !important;
    flex-direction: row-reverse !important;
    justify-content: space-between !important;
    width: 100% !important;
    --pf-c-expandable-section__toggle--PaddingTop: 16px;
    --pf-c-expandable-section__toggle--PaddingRight: 16px;
    --pf-c-expandable-section__toggle--PaddingBottom: 16px;
    --pf-c-expandable-section__toggle--PaddingLeft: 16px;
  }

  .pf-c-expandable-section__toggle-icon{
    color: #FFFFFF !important;
  }

  .pf-c-expandable-section__toggle-text{
    color: #FFFFFF !important; 
    font-weight: 600 !important;
  }

  .pf-c-expandable-section__content{
    --pf-c-expandable-section__content--MarginTop: 0;
  }

  .few-results-wrapper-desktop{
    width: 100%;
    font-size: 16px;
    margin: 32px 0 24px;
  }

  .few-results-wrapper-mobile{
    display: none;
  }

  .input-wrapper{
    max-width: 100%;
    width: 100%;
  }

  .results-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-self: center;
    width: 100%;
    margin-right: 0;
    margin-top: 16px;
    max-width: 100%;
  }

  .results-wrapper-hidden{
    display:none;
  }

  .result-card-wrapper{
    width:90%;
  }

  .total-results-container{
    width: 90%;
    margin: 16px 0 0;

  }

  .search__pagination-wrapper{
    display: none;
  }
  .search__footer-wrapper{
    display: block;
    width: 100%;
    align-items: center;
  }
  .total-results__bottom{
    display: none;
  }
  .search__footer-wrapper .pf-c-pagination__nav{
    padding-left: 0;
  }
  .pf-c-pagination{
    align-items: center !important;
  }
}

/* MOBILE STYLES */
@media(max-width: 480px) {
  .app-wrapper{
    display: flex;
    flex-direction: column;
  }

  .search__input-pagination-wrapper{
    width: 90%;
    margin: auto;
  }

  .single-skeleton-container {
    margin-bottom: 32px;
  }

  .loading-container {
    margin-top: 0;
  }

  .total-results-sort-by-container{
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  .filters-container-mobile{
    margin: 24px 0 16px;
  }

  .filter-text{
    margin-top: 0;
  }


  .pf-c-expandable-section__toggle{
    width: 100% !important;
  }

  .pf-c-expandable-section__content{
    width: 100% !important;
  }
  

  .few-results-wrapper-mobile{
    display: block;
    width: 90%;
    border-top: none;
    border-bottom: none;
    padding: 0;
    margin: 8px 0 24px;
  }

  .few-results-wrapper-desktop{
    display: none;
  }

  .few-results-text{
    font-size: 16px;
  }
  
  .few-results-text-link{
    font-size: 16px
  }

  .result-card-wrapper{
    width:100%;
  }

  .total-results-container{
    margin: 16px 24px 0;
    flex-direction: column;
  }

  .total-results-heading-text{
    font-size: 20px;
  }

  .num-results-text, .no-results-with-filter-text, .no-results-with-filter-text-query{
    font-size: 16px;
  }

  .num-results-container{
    margin-bottom: 16px;
  }
  .empty-state-container{
    width: 100%;
  }

  .suggestion-state-wrapper{
    width: 100%;
  }

  .card-container{
    width: 100%;
  }

  .search__input-form-wrapper{
    width: 100%;
  } 

  .search__pagination-wrapper{
    display: none;
  }
  .total-results{
    font-family: 'Red Hat Text';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    display: flex;
    align-items: center;
    color: #000000
  }
  .search__footer-wrapper{
    display: block;
  }
  .search__footer-wrapper .pf-c-pagination__nav{
    padding-left: 0;
  }
  .total-results__bottom{
    display: none;
  }
}