.search-page-filter-checklist{
    padding-left: 2px;
    --pf-c-list--ul--ListStyle: none;
    position: relative;
    max-height: 355px;
    overflow-y: auto;
    overflow-x: hidden;
}



  

.product-version-dropdown > .pf-c-select__menu{
    overflow-x: visible;
    overflow-y: visible;
  }

.pf-c-button{
    margin-top: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 21px;
}

.expand-button{
    padding-left: 24px !important;
    margin-top: 16px !important;
}


.filter-checklist-item-container{
    padding: 4px 24px;
}

.filter-checklist-item-container:hover{
    background: #EEEEEE;
}

.helper-text {
    margin: 10px 12px 0;
}

/* facet AB test styles */

.facet-filter-test-wrapper{
    display: flex;
}

.facet-filter-test-text-wrapper{
    flex: 1
}

.facet-filter-test-text-wrapper{
    display: flex;
    justify-content: space-between;
}

.facet-filter-test-num-results{
    font-family: 'Red Hat Text';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 21px;
    color: #6A6E73;
}

.pf-c-check__label{
    flex: 1;
}

   