.error-state__wrapper{
  margin-top: 64px;
}

.error-state-svg{
  width: 100%;
  margin: auto;
}

.error-state__text-content{
  margin: 48px 0;
  text-align: center;
}

.error-state__text {
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  color: #151515;
}

.error-state__button-container{
  text-align: center;
}

.pf-c-button{
  --pf-c-button--PaddingRight: 48px;
  --pf-c-button--PaddingLeft: 48px;
  margin-top: 0;
}

/* mobile styles */
@media(max-width: 480px) {
  .error-state__wrapper{
    margin-top: 48px;
  }
  .error-state__text-content{
    margin: 36px 0;
  }
  p {
    font-size: 18px;
  }
}